.projects {
  h2 {
    text-align: left;
    text-transform: uppercase;
  }
}

.projects__container {
  height: 100%;
}

.projects__wrapper {


  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 50px;
  grid-template-rows: auto;

  width: 100%;

  @include reset-ul();
  margin-bottom: 50px;
}

.projects__block {

  &:nth-child(2n) {
    .projects__stack {
      right: 20px;
    }
  }

  z-index: 6;
  position: relative;

  border-radius: 10px;
  color: $color-faded-shadow;
  background-color: $color-default-white;
  background-color: $color-primary-black;

  position: relative;


  &:hover {
    .projects__scroll {
      display: block;
    }

    .projects__item {
      transform: none
    }

    .projects__header {
      background-color: $color-faded-highligt;
    }

    .projects__stack {
      transform: translateX(-60px);

      transition-duration: 1s;
      z-index: -1;

      opacity: 1;
    }

    &:nth-child(2n) {
      .projects__stack {
        transform: translateX(60px);

        & svg {
          fill: $color-primary-black;

        }

      }

    }

    img {
      filter: none;
    }
  }


  picture {
    &::-webkit-scrollbar {
      width: 0;
    }

    width: 100%;
    height: 350px;
    display: block;
    overflow-y: scroll;

    scrollbar-width: none;
    border-radius: 0 0 10px 10px;
    position: relative;


  }

  img {
    filter: saturate(0);
  }
}

.projects__item {
  width: 100%;
  // height: 100%;
  position: relative;
  border: 1px solid $color-primary-black;
  top: -15px;
  left: -15px;
  border-radius: 10px;
  display: block;
  background-color: $color-faded;
  transform: translateY(10px);
  transition-duration: 0.5s;
  background-size: cover;
  overflow: hidden;
}

.projects__stack {

  background-color: transparent;
  @include reset-ul();
  position: absolute;

  top: -15px;

  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 100%;
  opacity: 0;



  svg {

    fill: $color-secondary;


    &:hover {
      fill: $color-secondary-shadow;
      transform: scale(1.2);
    }
  }
}

.projects__header {
  h3 {
    font-family: "Press";
    color: $color-primary-black;
  }

  margin: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 15%;
  border-bottom: 1px solid $color-primary-black;
  position: relative;
  color: $color-primary-black;
  padding: 0 15px;
}

.projects__controls {


  width: 30%;
  display: grid;
  grid-template-columns: auto auto;
  grid-column-gap: 10px;
}

.projects__control {
  // font-family: "Press";

  cursor: pointer;
  color: $color-secondary-border;
  font-size: 16px;
  font-weight: 800;


  padding: 5px;
  background: $color-secondary-highlight;
  border: 1px solid $color-primary-black;
  border-radius: 8px;
  transition: transform 0.15s;
  transform-style: preserve-3d;

  &:hover {
    transform: scale(1.02);
  }

  &--code {
    background-color: $color-secondary-highlight;
  }

  &--preview {
    background-color: $color-hover-yellow;
  }
}


@include vp-tablet {
  .projects__wrapper {

    grid-template-columns: auto;
    grid-row-gap: 80px;
    width: 70%;
    margin: auto;
  }

  .projects__block {
    &:nth-child(2n) {
      .projects__stack {
        & svg {
          fill: $color-secondary-shadow;

        }
      }
    }

    &:hover {
      .projects__scroll {
        display: none;
      }

      .projects__item {
        transform: translateY(10px);
      }

      .projects__header {
        background-color: $color-faded;
      }

      .projects__stack {
        transform: none;

        z-index: 100;
      }

      &:nth-child(2n) {
        .projects__stack {
          transform: none;

          & svg {
            fill: $color-secondary-shadow;

          }

        }
      }

    }

    picture {
      height: fit-content;
      overflow: hidden;
      max-height: 440px;
    }

    img {
      width: 100%;
      filter: none;
    }

    background: none;
  }

  .projects__item {
    top: 0;
    left: 0;
  }

  .projects__stack {
    transform: none;
    // left: -70px;
    opacity: 1;
    top: 102%;
    flex-direction: row;
    // position: relative;
    width: 100%;
    height: auto;


    li svg {
      fill: $color-primary-black;

    }
  }

}
